.messagePage__section {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.getMessage {
  padding: 1.5rem 1rem;
  height: 550px;
  margin-right: 5px;

  background-color: #fff;
  border-radius: 16px;
  border: 1px solid #e5e7eb;
  overflow: scroll;

  &__title {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 1.5rem;
  }

  &__pustoyTitle {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 150%;
    text-align: center;
    color: #6b7280;
  }

  &__box {
    display: flex;
    flex-direction: column;
    margin: 1rem 0;
    padding: 0 5px;

    &--name {
      font-family: "Inter";
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      margin-bottom: 0.5rem;

      span {
        font-size: 20px;
      }
    }

    &--head {
      display: flex;
      justify-content: space-between;

      p {
        font-family: "Inter";
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        margin-bottom: 0.5rem;

        span {
          font-size: 16px;
        }
      }
    }

    &--head-date {
      color: #6b7280;
      font-size: 12px;
      font-weight: 500;
    }

    &--des {
      color: #6b7280;
      font-family: "Inter";
      font-style: normal;
      font-weight: 500;
      line-height: 20px;
      font-size: 15px;
      margin: 1rem;
    }

    &--bottom {
      display: flex;
      justify-content: end;

      button {
        font-family: "Inter";
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        padding: 7px 17px;
        border-radius: 12px;
        border: none;
        cursor: pointer;
      }

      .delete {
        color: #9b1c1c;
        background: #fbd5d5;
        margin-right: 0.5rem;
      }

      .noDelete {
        color: #03543f;
        background: #def7ec;
      }
    }
  }
}

