@font-face {
  font-family: "Inter";
  src: local("Inter"), url(../Fonts/Inter-Medium.ttf) format("opentype");
}

html {
  scroll-behavior: smooth;
}

body {
  font-size: 16px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background: #f3f4f6;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
